import React from 'react'

function FlowButtons(props) {
    return (
        <div className="flowButtonsContainer">
            <div className="flowButtons">
                <button onClick={props.startFunction}>Start</button>
                <button onClick={props.paused ? props.resumeFunction : props.pauseFunction}>{props.paused ? "Resume" : "Pause"}</button>
                <button onClick={props.stopFunction} >Stop</button>
            </div>
        </div>
    )
}
FlowButtons.defaultProps = {
    startFunction:()=>{console.log("no start function")},
    stopFunction:()=>{},
    pauseFunction:()=>{},
    resumeFunction:()=>{},
    paused:false,
}
export default FlowButtons
