import React from 'react'

function Video() {
    function setVideoSrc(){
        console.log("settng src")
        var newSrc = document.getElementById("videoSrcInput").value
        document.getElementById("iframe").src = newSrc
    }
    return (
        <div>
            <iframe 
                id='iframe'
                width="560" 
                height="315" 
                src="https://www.youtube.com/embed/jgjHUUiq5EM" 
                title="YouTube video player" 
                frameborder="0" 
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen="allowfullscreen"
            >

            </iframe>
            <div>
                <input id='videoSrcInput' placeholder='Iframe src'></input>
                <button onClick={setVideoSrc}>SetIframe</button>
            </div>
        </div>
    )
}

export default Video
