import React from 'react'
import {useEffect} from 'react'
import {useState} from 'react'

import CircleButton from './CircleButton.js'
function Header(props) {

    return (
        <div className="header">            
            <CircleButton menuClass=" sideMenu" icon={"menu"}>
                <div onClick={props.newNoteProp} className="noteLine"> + New + </div>
                {props.titles.map(title=>
                    (<div className="menuItem" key={title.key} onClick={()=>props.loadNote(title.key)}>
                        <CircleButton icon={"menu"} menuClass=" titleMenu">
                            <CircleButton  onClick={()=>props.editNote(title.key)} noMenu={true}  icon={"edit"}></CircleButton>
                            <CircleButton  onClick={()=>props.deleteNote(title.key)} noMenu={true}  icon={"delete"}></CircleButton>                            
                        </CircleButton>
                        {title.title}
                    </div>)
                )}  
                <button onClick={props.setLanguageSpannish}>Spanish</button>                              
                <button onClick={props.setLanguageEnglish}>English</button>                              
                
            </CircleButton>
            <CircleButton  icon={"person"} menuClass="rightMenu">

            </CircleButton>
        </div>
    )
}
Header.defaultProps={
    titles:[{title:"default title", key:"default key"}],
    loadNote:()=>{},
    editNote:()=>{console.log("edit note not function")},
    newNoteProp:()=>{console.log("abc")}
}
export default Header
