import logo from './logo.svg';
import './App.css';
import {useState} from 'react'
import {useEffect} from 'react'
import Header from './components/Header.js'
import NoteDisplay from './components/NoteDisplay.js'
import FlowButtons from './components/FlowButtons.js'
import NoteEdit from './components/NoteEdit.js'
import Video from './components/Video.js'
import {initializeApp} from 'firebase/app' 
import {getDatabase, onValue, ref, set, push, remove} from 'firebase/database' 

function App() {

  //#region Variables
  const [titles, setTitles] = useState([{title:"init title", key:"init key"},{title:"init title 2", key:"init key 2"}])
  const [edit, setedit] = useState(false)
  const [lines, setlines] = useState(["load a note"])
  //const [counter, setCounter] = useState(0)
  const [pausedState, setPausedState] = useState(false)
  const [editNoteState, seteditNote] = useState("note")
  const [editTitleState, seteditTitle] = useState("title")
  const [editKeyState, setEditKey] = useState("key")
  const [isNewNote, setisNewNote] = useState(false)
  var paused = false
  var counter = 0
  var timeout = null
  var u = new SpeechSynthesisUtterance()
  u.rate = 0.5
  //#endregion
    
  //#region Firebase Init
  //https://console.firebase.google.com/u/0/project/defaultproject-c023e/database/defaultproject-c023e-default-rtdb/data
  var app = initializeApp({
    apiKey: "AIzaSyBDWCKZwSBi_Qp4U0u3D2tKrcIU290IrDE",
    authDomain: "defaultproject-c023e.firebaseapp.com",
    databaseURL: "https://defaultproject-c023e-default-rtdb.firebaseio.com",
    projectId: "defaultproject-c023e",
    storageBucket: "defaultproject-c023e.appspot.com",
    messagingSenderId: "147977670881",
    appId: "1:147977670881:web:fe1532718095f374bbe7a0",
    measurementId: "G-VY1DMS0BKY"
  })
  var db = getDatabase(app)
  var running = false 
  //#endregion

  //#region Firebase Functions
  useEffect(() => {
    loadTitles()
    return () => {
  
    }
  }, [])
  function loadTitles(){
    onValue(ref(db, "note-app/titles/"), titlesSnap=>{
      var tempArray = []    
      titlesSnap.forEach(title=>{
        tempArray.push({title:title.val(), key:title.key})
      })      
      setTitles(tempArray)      
    })
  }
  function loadNote(key){
    var noteLinesTemp = []
    onValue(ref(db, "note-app/notes/"+key), noteSnap =>{
      
      var note = noteSnap.val()
      if(note[0] == "#"){
        note = NumbersToString(note)
      }


      // if(noteSnap != null && noteSnap.val() != null)
      //   noteLinesTemp = noteSnap.val().split("\n")
      noteLinesTemp = note.split("\n")
      
      setlines(noteLinesTemp)
    })
  }
  function editNote(key){
        
    // load note and title into state
    setEditKey(key)
    onValue(ref(db, "note-app/notes/"+key), note =>{
      var note = note.val()
      if(note[0] == "#"){
        note = NumbersToString(note)
        console.log("edit has #")
      }
      else
        console.log("edit has no #")
      seteditNote(note)
    })
    onValue(ref(db, "note-app/titles/"+key), title => seteditTitle(title.val()))

    setisNewNote(false)

    // Show edit window
    setedit(true)    
  }
  function saveNote(key, note, title){
    // Save the title
    set(ref(db, "note-app/titles/"+key), title)
    
    // If the note has a # convert it
    var noteToSave = note
    if(note[0] == "#" )
      noteToSave = StringToNumbers(noteToSave)
    
    // Save the note
    set(ref(db, "note-app/notes/"+key), noteToSave)
  }
  function newNote(){    
    seteditNote("")
    seteditTitle("")
    setEditKey("")

    setisNewNote(true)

    setedit(true)
  }
  function saveNewNote(note, title){
    var newKey = 4
    newKey = push(ref(db, "note-app/titles")).key
    saveNote(newKey, note, title)    
    setedit(false)
  }
  function deleteNote(key){
    remove(ref(db, "note-app/titles/"+key))
    remove(ref(db, "note-app/notes/"+key))
  }
  //#endregion

  //#region Flow Buttons
  function start(){
    if(!running){
      running = true
      setPausedState(false)
      paused=false
      next()
    }
  }
  function stop(){
    setPausedState(true)
    //paused = true
    running = false
    //counter = 0
    clearTimeout(timeout)
  }
  function pause(){
    //setPausedState(true)
    //paused=true
    running = false
    clearTimeout(timeout)
  }
  function resume(){
    setPausedState(false)
    paused = false
    next()
  
  }
  //#endregion

  
  //loadVoices()

  //#region Speech
  
  function speak(text){
    //var u = new SpeechSynthesisUtterance()
    u.text = text
    window.speechSynthesis.speak(u)
  }
  function next(){    

    // check counter
    if(counter >= (lines.length))
        counter = 0

    // add modifiers and speak the line
    var newLine = modLine(lines[counter])
    
    speak(newLine)

    // increment counter
    counter++
    if(counter >= (lines.length))
        counter = 0

    // call again
    timeout = setTimeout(next, 5000)
  }
  function modLine(text){
    var m = ""
    m = text
    
    m = m.replaceAll("  ", " ")
    m = m.replaceAll("//", " ")
    m = m.replaceAll("_", " ")
    m = m.replaceAll(" , ", " comma, ")
    m = m.replaceAll("(", "par, ")
    m = m.replaceAll("[", " square, ")
    m = m.replaceAll("-", " dash, ")
    m = m.replaceAll(" ", ", ")
    m = m.replaceAll("=>", " arrow, ")
    
    return m
  }
  function setCounter(c){
    counter = c
    console.log("counter is now: "+counter)
  }
  
  //#endregion

  //#region Misc Functions
  var voices = "not loaded yet"
  function loadVoices(){
    voices = window.speechSynthesis.getVoices()
    console.log("voices length: "+voices.length)

    
    voices.forEach(function(voice, i) {
      // Create a new option element.
      var option = document.createElement('option');
      
      // Set the options value and text.
      option.value = voice.name;
      option.innerHTML = voice.name;
      
      var voiceSelect=document.getElementById("voiceSelect")
      // Add the option to the voice selector.
      voiceSelect.appendChild(option);
    });
  }
  window.speechSynthesis.onvoiceschanged = function(e) {
    //loadVoices();
  };
  //loadVoices()
  function speakSpanish(){

    var u2 = new SpeechSynthesisUtterance("hola como esta")
    var voices = window.speechSynthesis.getVoices()
    
    //u2.voice = speechSynthesis.getVoices().filter(function (voice) { return voice.name === "Google italiano"; })[0];
    u2.voice = voices[7]
    //u2.rate = 0.5
    
    console.log("lang: "+u2.lang)
    //u2.lang = "es-ES"
    console.log("lang: "+u2.lang)
    console.log(voices)
  
    //u2.lang = "ca-ES"
    window.speechSynthesis.speak(u2)
  }
  function setLanguageSpannish(){
    var voices = window.speechSynthesis.getVoices()
    if(voices.length>=9)
      u.voice=voices[8]
  }
  function setLanguageEnglish(){
    var voices = window.speechSynthesis.getVoices()
    if(voices.length>=3)
      u.voice=voices[2]
  }
  function loadTitlesTest(){
    var tempTitles = [{title:"title 1", key:"key1"},{title:"title 2", key:"key2"}]
    setTitles(tempTitles)
  }
  function log(text){
    console.log(text)
  }
  function closeEdit(){
    setedit(false)
  }
  function testFetch(){
    fetch('https://texttospeech.googleapis.com/v1beta1/text:synthesize', {
      method:'POST',
      body:{
        "audioConfig": {
          "audioEncoding": "LINEAR16",
          "pitch": 0,
          "speakingRate": 1
        },
        "input": {
          "text": "Google Cloud Text-to-Speech enables developers to synthesize natural-sounding speech with 100+ voices, available in multiple languages and variants. It applies DeepMind’s groundbreaking research in WaveNet and Google’s powerful neural networks to deliver the highest fidelity possible. As an easy-to-use API, you can create lifelike interactions with your users, across many applications and devices."
        },
        "voice": {
          "languageCode": "en-US",
          "name": "en-US-Wavenet-F"
        }
      }
    } ).then(res => console.log(res))
  }
  function test(){
    speakSpanish()
    //console.log("paused var: "+paused+" paused state: "+pausedState)
  }
  function NumbersToString(input){
  

    if(!input)
      return ""
      
      input = input.replaceAll("    ", " ")
      input = input.replaceAll("    ", " ")      

    var word = "agb893"
    var output = ""
    var x = 1, y=0
    var charCodes = input.split(",")
    while(x<charCodes.length){
      output += String.fromCharCode(parseInt(charCodes[x++]) - word.charCodeAt(y++))
      y = y % word.length
    
    }        

    return "#,"+output
  }
  function StringToNumbers(input){    
    
    if(!input)
      return ""

    console.log("s2n input:")
    console.log(input)
    console.log("input length:")
    console.log(input.length)

    input = input.replaceAll("    ", " ")
    input = input.replaceAll("    ", " ")

    var word = "agb893"
    var output = ""
    var x=2, y=0
    while(x<input.length){
      output += (input.charCodeAt(x++) + word.charCodeAt(y++))+", "
      y = y % word.length


    }

    return "#,"+output
  }
  //#endregion


  return (
    <div className="App">
      
      <Header 
        titles={titles} 
        loadNote={loadNote} 
        editNote={editNote}  
        deleteNote={deleteNote}
        newNoteProp={newNote}   
        setLanguageSpannish={setLanguageSpannish}
        setLanguageEnglish={setLanguageEnglish}
                   
        >          
      </Header>
      {/* <select id="voiceSelect"></select> */}
      <FlowButtons
        startFunction={start}
        stopFunction={stop}
        pauseFunction={pause}
        resumeFunction={resume}
        paused={pausedState}
      >
      </FlowButtons>      

      <button onClick={test}>Test</button>

      <NoteDisplay lines={lines} setCounter={setCounter}></NoteDisplay>
      
      {edit && <NoteEdit 
        closeEdit={closeEdit}
        note={editNoteState}
        title={editTitleState}
        saveFunction={saveNote}
        key={editKeyState}
        keyProp={editKeyState}
        saveNewNote={saveNewNote}
        isNewNote={isNewNote}
      ></NoteEdit>}
      
      <Video></Video>

    </div>
  );
}

export default App;
