import React, { useEffect } from 'react'

function NoteEdit(props) {
    useEffect(()=>{
        document.getElementById("noteArea").value = props.note
        document.getElementById("titleInput").value = props.title
    },[])
    function save(){
        var note = document.getElementById("noteArea").value
        var title = document.getElementById("titleInput").value
        props.saveFunction(props.keyProp, note, title)        
    }
    function saveNew(){
        var note = document.getElementById("noteArea").value
        var title = document.getElementById("titleInput").value
        props.saveNewNote(note, title)
        //props.closeEdit()
    }

    function setNoteCheck(){
        setTimeout(checkNote, 500)
    }
    function checkNote(){
      
        //document.getElementById("noteArea").value = note
      
        // even though called from setTimeout it still works with the prop
        document.getElementById("noteArea").value = props.note
    }

    // will this updae when prop updates?
    //var note = props.note
    setNoteCheck()
    // can have functions that set var to state 
    // will be called when state changes because will refresh
    // then on timer call function that updats the values
    return (
        <div className="noteEditContainer">
            <div className="noteEdit">
                <div style={{position:'relative'}}>
                    <div className="closeButton" onClick={props.closeEdit}>x</div>
                </div>
                
                <input id="titleInput" placeholder="title"></input>
                <textarea id="noteArea" placeholder="note goes here"></textarea>
                <div className="bottomButtonContainer">
                    <button onClick={props.closeEdit}>Cancel</button>
                    <button onClick={props.isNewNote ? saveNew : save}>Save</button>
                </div>     
                {/* 
                    the note was not updating because the html loaded before the note was loaded
                    so the useEffect functino was called before the note was loaded
                    this makes the element refresh when note is updated
                */}
                {props.note}
            </div>
        </div>
    )
}
NoteEdit.defaultProps={
    closeEdit:()=>{},
    saveFunction:()=>{},
    note:"loading",
    title:"loading",    
    isNewNote:false,
}

export default NoteEdit
