import React from 'react'
import logo from '../logo.svg'

function NoteDisplay(props) {
    //console.log("lines length: "+props.lines.length)
    return (
        <div className="noteDisplay">
            <div className="menuItem"></div>            
            {props.lines.map((line, i)=>
                (<div className="noteLine" key={i} onDoubleClick={()=>props.setCounter(i)} >{line}</div>)
            )}
            {props.children}
            {props.lines.length==1 && <img src={logo} className="App-logo" alt="logo" />}
            
        </div>
    )
}
NoteDisplay.defaultProps={
    lines:[],
    setCounter:()=>{},
}
export default NoteDisplay
